.adminLoginPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 69px;

  .logo {
    width: 40px;
    height: 40px;
  }

  h3 {
    font-weight: 700;
  }

  .account,
  .password {
    width: 356px;
  }

  .authButton {
    border-radius: 50px;
    background-color: #ff6600;
    border: none;
    width: 356px;
    height: 46px;

    font-size: 20px;
    color: white;
    min-width: 300px;
    padding: 8px 24px;
    margin-top: 8px;

    &:hover {
      cursor: pointer;
    }
  }

  .linkroute {
    display: flex;
    justify-content: flex-end;
    margin-top: 16px;
    width: 365px;

    span {
      font-size: 16px;
    }
  }
}
