.signUpPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 69px;
  padding-bottom: 69px;


  .logo {
    width: 40px;
    height: 40px;
  }

  h3 {
    font-weight: 700;
  }

  .account,
  .userName,
  .email,
  .password,
  .passwordCheck {
    width: 356px;
  }

  .authButton {
    border-radius: 50px;
    background-color: #ff6600;
    border: none;
    width: 356px;
    height: 46px;

    font-size: 20px;
    color: white;
    min-width: 300px;
    padding: 8px 24px;
    margin-top: 8px;

    &:hover {
      cursor: pointer;
    }
  }

  .linkroute { 
    margin-top: 16px;
    span {
      font-size: 16px;
    }
  }
}